.activity{
    color: white;
    margin-top: 4.5rem;

    &__info{
        display: flex;
        justify-content: space-between;
    }

    &__container{
        margin-top: 1rem;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            top: 0;
            left: 27%;
            height: 90%;
            z-index: -1;
            border-left: 3px dotted white;
        }

        .transaction{
            display: grid;
            grid-template-columns: 0.8fr 0.3fr 2fr;
            margin-bottom: 2rem;

            .dot{
                height: 0.6rem;
                width: 0.6rem;
                background-color: #f94144;
                border-radius: 1rem;
            }
        }
    }
}