.sidebar{
    background-color: #212529;
    color: white;

    .brand{
        background-color: black;
        height: 10%;
        display: flex;
        justify-content: center;
        align-items: center;

        h2{
            font-weight: 900;
            letter-spacing: 0.3vw;

            span{
                color: #c70f61;
                font-weight: 900;
            }
        }
    }

    .links{

        li{
            list-style-type: none;
            margin: 0 auto;
            width: 60%;
            margin-top: 2rem;

            &:nth-of-type(1){

                a{
                    color: #c70f61;
                }
            }
            a{
                color: white;
                text-decoration: none;
                text-transform: uppercase;
                display: flex;
                gap: 0.7rem;
                transition: 0.5s ease-in-out;

                &:hover{
                    color: #c70f61;
                }
            }
        }
    }
}