         
    .wallets {
        color: white;
        width: 100%;
        &__info {
          display: flex;
          justify-content: space-between;
          margin-bottom: 1rem;
        }
        &__container {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 2rem;
          .wallet {
            display: flex;
            gap: 2rem;
            padding: 1.5rem;
            background-color: rgba(54, 54, 54, 0.473);
            height: 9rem;
            width: 100%;
            border-radius: 0.5rem;
            img {
              height: 4rem;
              width: 4rem;
              border-radius: 4rem;
              transition: 0.4s ease-in-out;
              &:hover {
                transform: scale(1.1);
              }
            }
            &__info {
              display: flex;
              flex-direction: column;
              gap: 1rem;
              &__balance {
                display: flex;
                align-items: center;
                gap: 3rem;
                h4 {
                  width: 50%;
                }
              }
              &__price {
                display: flex;
                align-items: center;
                gap: 3rem;
                h4 {
                  width: 50%;
                }
              }
            }
          }
        }
      }
